import React from 'react'
import './Navbar.css'
import { NavLink } from 'react-router-dom'

function Navbar() {
  return (
      <nav className="navbar  navbar-expand-lg bg-dark fixed-top top-0">
        <div className="container">
          <NavLink className="navbar-brand branding" aria-current="page" >
            <h1 className='text-white '>Youssef</h1>
          </NavLink>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <i class="fa-solid fa-y text-white "></i>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink className="nav-link  me-2  text-white " to="projects">
                  Projects
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link  me-2  text-white " to="skills">
                  Skills
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link  me-2  text-white " to="certificates">
                Certificates
                </NavLink>
              </li>
             
            </ul>
          </div>
        </div>
      </nav>
  )
}

export default Navbar
